.main {
  padding-bottom: 60px;
  &__inner {
    background-image: url("../img/main-img.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 20px;
    padding: 30px 60px;
  }
  &__group {
    max-width: 800px;
    background: rgba(255, 255, 255, 0.7);
    backdrop-filter: blur(6px);
    border-radius: 20px;
    padding: 40px;
  }
  &__title {
    font-size: 48px;
    line-height: 130%;
    font-weight: 500;
  }
  &__subtitle {
    font-size: 30px;
    line-height: 130%;
    font-weight: 500;
    margin-bottom: 40px;
  }
  &__list {
    margin-bottom: 40px;
    li {
      font-size: 21px;
      line-height: 130%;
    }
  }
  &__btn {
    height: 58px;
    gap: 10px;
    padding: 5px 50px;
    .btn-text {
      font-size: 18px;
    }
  }
}
.info {
  padding: 60px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 15px;
  }
  &__desc {
    width: 330px;
    flex-shrink: 0;
    background: #2b702b;
    border-radius: 20px;
    padding: 30px;
    color: #fff;
  }
  &__images {
    width: calc(100% - 345px);
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 30px / 3));
    gap: 15px;
  }
  &__img {
    position: relative;
    display: block;
    padding-bottom: 140%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 20px;
    }
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-bottom: 15px;
  }
  &__icon {
    width: 40px;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    img {
      width: 100%;
    }
  }
  &__info {
    background: #fff;
    border-radius: 15px;
    padding: 5px 10px;
    color: #212431;
    font-size: 18px;
    line-height: 130%;
    font-weight: 500;
  }
  &__text {
    font-size: 26px;
    line-height: 130%;
    margin-bottom: 15px;
  }
  &__text2 {
    font-size: 21px;
    line-height: 130%;
    font-weight: 300;
  }
}
.roadmap {
  padding: 60px 0 180px;
  &__title {
    margin-bottom: 10px;
  }
  &__subtitle {
    margin-bottom: 60px;
  }
  &__items {
    position: relative;
    z-index: 3;
    display: grid;
    grid-template-columns: repeat(5, calc(100% / 5 - 120px / 5));
    gap: 30px;
    margin-bottom: 60px;
  }
  &__item {
    position: relative;
    &:before {
      content: '';
      position: absolute;
      top: 80px;
      left: 1px;
      width: calc(100% + 29px);
      height: 3px;
      background: #212431;
      pointer-events: none;
    }
    &:after {
      content: '';
      position: absolute;
      z-index: 2;
      top: 70px;
      left: 0;
      width: 20px;
      height: 20px;
      background: #fff;
      box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.3);
      border-radius: 50%;
      pointer-events: none;
    }
    &-icon {
      max-width: 50px;
      height: 50px;
      margin-bottom: 60px;
      opacity: 0.5;
      img {
        max-width: 100%;
      }
    }
    &-price {
      display: none;
      background: #212431;
      border-radius: 7px;
      padding: 3px 10px;
      font-size: 12px;
      line-height: 130%;
      color: #fff;
      margin-bottom: 15px;
    }
    &-title {
      font-size: 21px;
      line-height: 130%;
      margin-bottom: 15px;
    }
    &-info {
      font-size: 21px;
      line-height: 130%;
      color: #007c03;
      margin-bottom: 15px;
      opacity: 0.75;
    }
    .orange {
      color: #db7000;
    }
    &-info2 {
      display: none;
      font-size: 18px;
      line-height: 130%;
      font-weight: 300;
      color: #000;
      opacity: 0.2;
    }
  }
  &__bottom {
    position: relative;
  }
  &__btn {
    position: relative;
    z-index: 2;
    height: 58px;
    gap: 10px;
    padding: 5px 50px;
    span {
      font-size: 18px;
    }
  }
  &__images {
    position: absolute;
    top: 50%;
    left: 350px;
    transform: translate(0, -50%);
  }
  &__img {
    transform: rotate(6deg);
    width: 200px;
    img {
      width: 100%;
    }
  }
  &__img2 {
    position: absolute;
    z-index: 2;
    top: 60px;
    left: 100px;
    transform: rotate(-6deg);
    width: 200px;
    img {
      width: 100%;
    }
  }
}
.excursion {
  padding: 60px 0;
  &__title {
    margin-bottom: 10px;
  }
  &__subtitle {
    font-size: 45px;
    margin-bottom: 60px;
    opacity: 0.55;
  }
  &__images {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 30px / 4));
    gap: 10px;
    margin-bottom: 30px;
  }
  &__img {
    position: relative;
    padding-bottom: 58%;
    &:nth-child(2) {
      grid-column: 2/4;
      grid-row: 1/3;
    }
    &:nth-child(3) {
      grid-column: 4/5;
      grid-row: 1/3;
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 15px;
    }
  }
  &__btn {
    height: 58px;
    gap: 10px;
    padding: 5px 50px;
    margin: 0 auto;
    .btn-text {
      font-size: 18px;
    }
  }
  &__img2 {
    max-width: 700px;
    margin: 0 auto 15px;
    img {
      width: 100%;
      border-radius: 15px;
    }
  }
  &__info {
    max-width: 700px;
    font-size: 14px;
    line-height: 130%;
    margin: 0 auto 30px;
  }
}
.land {
  padding: 60px 0;
  &__title {
    margin-bottom: 10px;
  }
  &__subtitle {
    margin-bottom: 60px;
  }
  &__flex {
    display: flex;
    align-items: stretch;
    gap: 30px;
    margin-bottom: 60px;
  }
  &__img {
    position: relative;
    width: calc(100% - 410px);
    padding-bottom: 46%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 15px;
    }
  }
  &__video {
    position: relative;
    width: 380px;
    flex-shrink: 0;
    padding-bottom: 46%;
    video {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 15px;
      object-fit: cover;
    }
  }
  &__bottom {
    z-index: 2;
  }
  &__images {
    top: unset;
    left: 200px;
    bottom: 50px;
    transform: unset;
  }
  &__btn {
    margin: 0 auto;
  }
}
.gallery {
  padding: 60px 0;
  &__title {
    max-width: 1165px;
    margin-bottom: 10px;
  }
  &__subtitle {
    margin-bottom: 60px;
  }
  &__images {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 60px / 4));
    gap: 20px;
  }
  &__img {
    position: relative;
    padding-bottom: 135%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 20px;
    }
  }
  &__group {
    margin-top: 60px;
  }
  nav {
    &:not(:last-child) {
      margin-bottom: 30px;
    }
    p {
      font-size: 18px;
      line-height: 130%;
      font-weight: 300;
    }
    ul {
      li {
        position: relative;
        padding-left: 15px;
        font-size: 18px;
        line-height: 130%;
        font-weight: 300;
        &:before {
          content: '';
          position: absolute;
          top: 7px;
          left: 0;
          width: 6px;
          height: 6px;
          background: #212431;
          border-radius: 50%;
        }
      }
      strong {
        font-weight: 700;
      }
    }
  }
  &__flex {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 30px;
  }
  &__items {
    width: 50%;
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 40px / 2));
    gap: 40px;
  }
  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background: #fff;
    box-shadow: 0px 0px 50px 5px rgba(33, 36, 49, 0.1);
    border-radius: 20px;
    padding: 20px 10px;
    &-icon {
      position: absolute;
      top: -30px;
      left: -15px;
      max-width: 64px;
      img {
        max-width: 100%;
      }
    }
    &-text {
      font-size: 33px;
      line-height: 130%;
      margin-bottom: 5px;
    }
    &-text2 {
      font-size: 16px;
      line-height: 130%;
      margin-bottom: 5px;
    }
    &-link {
      font-size: 12px;
      line-height: 130%;
      font-weight: 300;
      text-decoration: underline;
    }
  }
  &__img2 {
    position: relative;
    width: 50%;
    padding-bottom: 34%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      box-shadow: 0px 0px 50px 10px rgba(33, 36, 49, 0.1);
      border-radius: 15px;
    }
  }
  &__btn {
    height: 58px;
    gap: 10px;
    padding: 5px 50px;
    margin: 60px auto 0;
    .btn-text {
      font-size: 18px;
    }
  }
}
