.questions {
  padding: 60px 0;
  &__title {
    margin-bottom: 60px;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
  }
  &__item {
    border-bottom: 1px solid #e6e6e6;
    &:first-child {
      border-top: 1px solid #e6e6e6;
    }
    &-show {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 15px;
      padding: 25px 0;
      cursor: pointer;
    }
    &-title {
      font-size: 22px;
      line-height: 130%;
      font-weight: 500;
    }
    &-btn {
      position: relative;
      width: 24px;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      &:before {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 40px;
        height: 40px;
        border-radius: 50%;
        transition: all 0.3s;
      }
      svg {
        position: relative;
        z-index: 2;
        width: 100%;
        height: auto;
        transition: all 0.3s;
      }
    }
    &-hidden {
      display: none;
      margin-bottom: 25px;
    }
    &-text {
      font-size: 14px;
      line-height: 130%;
    }
  }
  .item-show {
    &:hover {
      .questions__item-btn {
        &:before {
          background: #e6e6e6;
        }
      }
    }
  }
  .item-show--active {
    .questions__item-btn {
      svg {
        transform: rotate(-45deg);
      }
    }
  }
}
.contacts {
  margin-top: 60px;
  &__inner {
    position: relative;
  }
  &__group {
    position: absolute;
    z-index: 2;
    top: 120px;
    left: 0;
    max-width: 460px;
    width: 100%;
    background: #2b702b;
    border-radius: 15px;
    padding: 60px;
    color: #fff;
  }
  &__title {
    font-size: 26px;
    line-height: 130%;
    margin-bottom: 30px;
  }
  &__time,
  &__phone,
  &__address {
    display: block;
    font-size: 16px;
    line-height: 130%;
    font-weight: 300;
  }
  &__time {
    margin-bottom: 15px;
  }
  &__phone {
    margin-bottom: 15px;
  }
  .map-wrap {
    position: relative;
    min-height: 600px;
    padding-bottom: 31.2%;
  }
  .map {
    position: absolute !important;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    iframe {
      width: 100%;
