.footer {
  background: #2b702b;
  padding: 60px 0;
  color: #fff;
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 15px 30px;
    margin-bottom: 30px;
    li {
      font-size: 14px;
      line-height: 130%;
    }
    button {
      background: transparent;
      padding: 0;
      color: #fff;
    }
  }
  &__logo {
    width: 150px;
    display: block;
    margin-bottom: 30px;
    img {
      width: 100%;
    }
  }
  &__text {
    text-align: center;
    opacity: 0.7;
    p {
      font-size: 14px;
      line-height: 130%;
    }
  }
}
