.wrapper {
  overflow: hidden;
}
.btn-black__border {
  position: relative;
  background: transparent;
  border: 1px solid #212431;
  border-radius: 15px;
  color: #212431;
  overflow: hidden;
  &:hover {
    box-shadow: 0px 10px 40px -2px rgba(0, 11, 48, 0.3) !important;
  }
  .btn-arrow {
    width: 24px;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    img {
      width: 100%;
    }
  }
  .btn-text {
    line-height: 120%;
  }
  .animation {
    position: absolute;
    top: 0;
    left: 0;
    transform: translateX(-30%);
    width: 100%;
    height: 100%;
    animation-name: flash;
    animation-duration: 2.3s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    pointer-events: none;
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 12%;
      background: linear-gradient(90deg, rgba(255, 255, 255, .1), rgba(255, 255, 255, .4));
      width: 45px;
      height: 100%;
      transform: skewX(-45deg);
    }
  }
}
.btn-black__green {
  position: relative;
  background: #2b702b;
  border-radius: 15px;
  box-shadow: 0px 0px 5px 5px rgba(33, 36, 49, 0.1);
  color: #fff;
  overflow: hidden;
  &:hover {
    box-shadow: 0px 10px 40px -2px rgba(0, 11, 48, 0.3);
  }
  .btn-arrow {
    width: 24px;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    img {
      width: 100%;
    }
  }
  .btn-text {
    line-height: 120%;
  }
  .animation {
    position: absolute;
    top: 0;
    left: 0;
    transform: translateX(-30%);
    width: 100%;
    height: 100%;
    animation-name: flash;
    animation-duration: 2.3s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    pointer-events: none;
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 12%;
      background: linear-gradient(90deg, rgba(255, 255, 255, .1), rgba(255, 255, 255, .4));
      width: 45px;
      height: 100%;
      transform: skewX(-45deg);
    }
  }
}
@keyframes flash {
  0% {
    transform: translateX(-30%);
  }
  30% {
    transform: translateX(105%);
  }
  100% {
    transform: translateX(105%);
  }
}
.h2 {
  font-size: 55px;
  line-height: 130%;
  font-weight: 500;
}
.subtitle {
  font-size: 48px;
  line-height: 130%;
  font-weight: 500;
  opacity: 0.2;
}
.modal {
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(33, 36, 49, 0.5);
  padding: 50px 0;
  opacity: 0;
  pointer-events: none;
  overflow-y: auto;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 100px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    position: relative;
    width: calc(100% - 30px);
    max-width: 560px;
    background: #fff;
    border-radius: 15px;
    padding: 40px;
    margin: 0 auto;
  }
  &__close {
    position: absolute;
    top: 20px;
    right: 20px;
    width: 45px;
    height: 45px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: transparent;
    padding: 5px;
    svg {
      max-width: 100%;
      height: auto;
    }
  }
  &__title {
    font-size: 36px;
    line-height: 130%;
    margin-bottom: 10px;
  }
  &__text {
    font-size: 14px;
    line-height: 130%;
    margin-bottom: 25px;
  }
  .form {
    display: grid;
    grid-template-columns: 100%;
    gap: 25px;
    &-group {
      input {
        width: 100%;
        height: 60px;
        border: none;
        border-bottom: 1px solid #212431;
        font-size: 16px;
        line-height: 130%;
        color: #212431;
        &::placeholder {
          color: #212431;
          opacity: 0.7;
        }
      }
    }
    &__btn {
      width: 100%;
      height: 60px;
      padding: 5px 10px;
      .btn-text {
        font-size: 18px;
      }
    }
    &__check {
      position: relative;
      input {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
      }
      input:checked ~ label {
        &:before {
          background-image: url("../img/check.svg");
        }
      }
      label {
        position: relative;
        display: block;
        padding-left: 25px;
        font-size: 13px;
        line-height: 130%;
        &:before {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 15px;
          height: 15px;
          background-repeat: no-repeat;
          background-position: center;
          background-size: contain;
          border: 1px solid #212431;
        }
        a {
          position: relative;
          z-index: 3;
          color: rgb(128, 127, 127);
        }
      }
    }
    .iti {
      width: 100%;
    }
    .iti__selected-flag {
      background: transparent !important;
    }
    .iti__selected-dial-code {
      order: 2;
    }
  }
}
#marquee-container {
  position: relative;
  width: 100%;
  height: 60px;
  background: #2b702b;
  white-space: nowrap;
}
#marquee {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  display: inline-block;
  white-space: nowrap;
  font-size: 24px;
  color: #fff;
}

@media screen and (max-width: 992px) {
  .h2 {
    font-size: 40px;
  }
  .subtitle {
    font-size: 32px;
  }
  .modal {
    &__dialog {
      max-width: 510px;
      padding: 30px;
    }
    &__title {
      font-size: 28px;
    }
  }
  #marquee-container {
    height: 48px;
  }
  #marquee {
    font-size: 18px;
  }
}

@media screen and (max-width: 576px) {
  .btn-black__border,
  .btn-black__green {
    border-radius: 10px;
    .btn-arrow {
      width: 20px;
    }
  }
  .h2 {
    font-size: 30px;
  }
  .subtitle {
    font-size: 24px;
  }
  .modal {
    &__dialog {
      padding: 30px 20px;
    }
  }
  #marquee-container {
    height: 40px;
  }
  #marquee {
    font-size: 14px;
  }
}
